<template>
  <div class="main ">
    <a-breadcrumb>
      <a-breadcrumb-item>首页</a-breadcrumb-item>
      <a-breadcrumb-item>零部件分类管理</a-breadcrumb-item>
    </a-breadcrumb>
    <h2 class="title">零部件分类管理</h2>
    <div class="search_box">
      <span>分类编码：</span>
      <a-input placeholder="输入分类编码" v-model="searchkey" />
      <span>分类名称：</span>
      <a-input placeholder="输入分类名称" v-model="searchtitle" />
      <a-button type="primary" @click="searchSubmit">查询</a-button>
    </div>

    <div class="content_box">
      <div class="content_title">
        <h3>零部件分类列表</h3>
        <a-button type="primary" @click="showAddClassificationVisible(0)">添加主分类</a-button>
        <a-button icon="download" @click="showModal5">批量导入</a-button>
      </div>
      <div class="content_table">
        <!-- <a-layout style="height:calc(100vh - 306px);overflow:auto "> -->

        <a-table
          :columns="columns"
          :dataSource="data"
          :pagination="showPagination"
          rowKey="claId"
          :scroll="{ y: 540 }"
          :rowSelection="rowSelection"
          class="classify-table-wyw"
        >
          
          <template slot="claCode" slot-scope="text, record" >
            <span class="lineEllipsis-class">{{text}}</span>
          </template>
          
          <template slot="claName" slot-scope="text, record">
            <span class="lineEllipsis-class">{{text}}</span>
          </template>
          <template slot="action" slot-scope="text, record">
            <!-- <a-button
              icon="plus"
              disabled
              v-if="record.level == 5"
              class="classbtn"
              @click="showAddClassificationVisible(record.claId)"
              style="border:none;background:none;color:#bbb;"
            >添加</a-button> -->

            <!-- <a-button icon="plus" v-else class="classbtn"  @click="showAddClassificationVisible(record.claId)" style="border:none;background:none;color:#00aaee;-webkit-box-shadow:none;box-shadow:none;">添加</a-button> -->

            <a
              href="javascript:;"
              aria-disabled="true"
              class="no-underline"
              @click="showAddClassificationVisible(record.claId)"
            >
              <a-icon type="plus" />添加
            </a>
            <a
              href="javascript:;"
              class="no-underline"
              @click="showEditClassificationVisible(record)"
            >
              <a-icon type="edit" />修改
            </a>

            <a-popconfirm
              v-if="record.children == null || record.children.length == 0"
              title="确定删除这条数据吗?"
              @confirm="() => onDelete(record.claId)"
              cancelText="取消"
              okText="确定"
            >
              <a href="javascript:;" class="no-underline">
                <a-icon type="delete" />删除
              </a>
              <!-- <a href="javascript:;"  v-if="record.children == null || record.children.length == 0"> <a-icon type="delete" />删除</a> -->
              <!-- <a-button icon="delete" class="classbtn"  style="border:none;background:none;color:#00aaee;-webkit-box-shadow:none;box-shadow:none;">删除</a-button> -->
            </a-popconfirm>

            <a-popconfirm
              v-else
              title="确定删除这条数据吗?"
              @confirm="() => onDelete(record.claId)"
              cancelText="取消"
              okText="确定"
            >
              <!-- <a href="javascript:;"  v-if="record.children == null || record.children.length == 0"> <a-icon type="delete" />删除</a> -->
              <a-button
                icon="delete"
                disabled
                class="classbtn class-del-btn-wyw"
              >删除</a-button>
            </a-popconfirm>

            <a
              href="javascript:;"
              aria-disabled="true"
              class="no-underline"
              @click="filter(record)"
            >
              <a-icon type="setting" />过滤条件
            </a>
          </template>
        </a-table>
        <!-- </a-layout> -->
        <div class="pagination-box">
          <a-pagination
            @change="onChangepagination"
            :pageSizeOptions="pageSizeOptions"
            :total="total"
            :showTotal="total => `共 ${total} 条`"
            showSizeChanger
            showQuickJumper
            :pageSize="pageSize"
            v-model="currentpage"
            @showSizeChange="onShowSizeChange"
          >
            <template slot="buildOptionText" slot-scope="props">
              <span v-if="props.value!=='50'">{{props.value}}条/页</span>
              <span v-if="props.value==='50'">全部</span>
            </template>
          </a-pagination>
        </div>
      </div>
    </div>

    <a-modal
      title="新增零部件分类"
      :visible="addClassificationVisible"
      @ok="addClassificationSubmit"
      :confirmLoading="confirmLoading"
      @cancel="handleAddClassificationVisible"
      cancelText="取消"
      okText="确定"
      class="new-classity-modal classification-modal-edit"
      width="900px"
    >
      <a-form :form="addClassificationForm"  style="position:relative;">
        <!--<a-form-item label="分类编码" :label-col="{ span: 3 }" :wrapper-col="{ span: 21 }">
          <a-input
            placeholder="输入分类编码"
            v-decorator="[ 'claCode', {rules: [{ required: true, message: '分类编码为空' }]}]"
            @blur.prevent="blurRed()"
          />
        </a-form-item>-->
        <a-form-item label="分类名称" :label-col="{ span: 3 }" :wrapper-col="{ span: 21 }">
          <a-input
            placeholder="输入分类名称"
            v-decorator="[ 'claName',{rules: [{ required: true, message: '分类名称为空' }]} ]"
            @blur.prevent="blurRedName()"
          />
        </a-form-item>

       <div class="gray-back-line"></div>
        <div style="height:6px;background:rgba(240, 242, 245, 1);"></div>
        <div style="font-weight: 800;height:39px;line-height:39px;text-indent:1px;">扩展属性</div>

        <a-table :columns="columnsfilter" :dataSource="dataSource" :pagination="false">
          <template
            v-for="(col, i) in ['attName','attType', 'attIsempty', 'attValue']"
            :slot="col"
            slot-scope="text, record"
          >
            <editable-cell
              :key="col"
              v-if="i==0 "
              :text="text"
              @change="onCellChangeattName(record.key, 'attName', $event)"
            />

            <a-select
              style="width: 120px"
              v-model="record.attIsempty"
              @change="handleChangeSelectAllowed(record.attIsempty,record.key,col)"
              v-if=" i==2 "
              :key="col"
            >
              <a-select-option
                :value="item.dataKey"
                v-for="item in SelectAllowedArr"
                :key="item.dataKey"
              >{{item.dataName}}</a-select-option>
            </a-select>

            <a-select
              style="width: 120px"
              v-model="record.attType"
              @change="handleChangeSelectType(record.attType,record.key,col)"
              v-if=" i==1"
              :key="col"
            >
              <a-select-option
                :value="item.dataKey"
                v-for="item in SelectTypeArr"
                :key="item.dataKey"
              >{{item.dataName}}</a-select-option>
            </a-select>

            <editable-cell
              :key="col"
              v-if="i==3 "
              :text="text"
              @change="onCellChangeattValue(record.key, 'attValue', $event)"
            />
          </template>

          <template slot="operation" slot-scope="text, record">
            <template>
              <a-popconfirm title="是否要删除此行？" @confirm="removefilter(record.key)">
                <a>删除</a>
              </a-popconfirm>
            </template>
          </template>
        </a-table>

        <a-button
          style="width: 100%; margin-top: 16px; margin-bottom: 8px"
          type="dashed"
          @click="newMeber"
        >
        <a-icon type="plus" />
          添加
        </a-button>
      </a-form>
    </a-modal>

    <a-modal
      class="classification-modal-edit"
      title="修改零部件分类"
      :visible="editClassificationVisible"
      @ok="eidtClassificationSubmit"
      :confirmLoading="confirmLoading"
      @cancel="handleEditClassificationVisible"
      cancelText="取消"
      okText="确定"
      width="900px"
    >
      <a-form :form="editClassificationForm" style="position:relative;">
        <!--<a-form-item label="分类编码" :label-col="{ span: 2 }" :wrapper-col="{ span: 22 }">
          <a-input placeholder="输入分类编码" v-decorator="[ 'claCode']" />
        </a-form-item>-->
        <a-form-item label="分类名称" :label-col="{ span: 2 }" :wrapper-col="{ span: 22 }">
          <a-input placeholder="输入分类名称" v-decorator="[ 'claName' ]" />
        </a-form-item>

        <div class="gray-back-line"></div>
        <div style="height:6px;background:rgba(240, 242, 245, 1);"></div>
        <div style="font-weight: 800;height:39px;line-height:39px;text-indent:1px;">扩展属性</div>

        <a-table
          :columns="columnsModifyClassification "
          :dataSource="dataSourceModifyClassification"
          :pagination="false"
        
        >
          <template
            v-for="(col, i) in ['attName','attType', 'attIsempty', 'attValue']"
            :slot="col"
            slot-scope="text, record"
          >
            <editable-cell
              :key="col"
              v-if="i==0 "
              :text="text"
              @change="onCellChangeattNameModify(record.key, 'attName', $event)"
            />

            <a-select
              style="width: 120px"
              v-model="record.attIsempty"
              @change="handleChangeSelectAllowed(record.attIsempty,record.key,col)"
              v-if=" i==2 "
              :key="col"
            >
              <a-select-option
                :value="item.dataKey"
                v-for="item in SelectAllowedArr"
                :key="item.dataKey"
              >{{item.dataName}}</a-select-option>
            </a-select>

            <a-select
              style="width: 120px"
              v-model="record.attType"
              @change="handleChangeSelectType(record.attType,record.key,col)"
              v-if=" i==1"
              :key="col"
            >
              <a-select-option
                :value="item.dataKey"
                v-for="item in SelectTypeArr"
                :key="item.dataKey"
              >{{item.dataName}}</a-select-option>
            </a-select>

            <editable-cell
              :key="col"
              v-if="i==3 "
              :text="text"
              @change="onCellChangeattValueModify(record.key, 'attValue', $event)"
            />
          </template>

          <template slot="operation" slot-scope="text, record">
            <template>
              <a-popconfirm title="是否要删除此行？" @confirm="removeModify(record.key,record)">
                <a>删除</a>
              </a-popconfirm>
            </template>
          </template>
        </a-table>
        <a-button
          style="width: 100%; margin-top: 16px; margin-bottom: 8px"
          type="dashed"
          @click="newMeberModifyClassification"
        >
         <a-icon type="plus" />
          添加
        </a-button>
      </a-form>
    </a-modal>

    <!-- 批量导入弹窗 -->
    <a-modal
      title=" 批量导入"
      :visible="visible5"
      class="dialog_content classify-muti-modal"
      width="900px"
      :confirmLoading="confirmLoading"
      @cancel="handleCancel5"
      @ok="handleOkImport"
       :footer="null"
    >
      <div style="width:800px;margin:30px auto 0;">
        <a-steps :current="current" labelPlacement="vertical">
          <a-step v-for="item in steps" :key="item.title" :title="item.title" />
        </a-steps>
        <div class="steps-content">
          <div v-if="current === 0" @nextStep="nextStep">
            <div class="import-introduce-step">
              <div class="import-introduce-step-left">
                <img src="../assets/u579.png" alt="">
              </div>
              <div class="import-introduce-step-rigth">
                <div class="text-align-left">
                  <p
                    style="font-weight:700;font-size: 16px;"
                    class="line-padding-import-introduce-step"
                  >填写导入零部件的信息</p>
                  <p
                    class="line-padding-import-introduce-step"
                  >请按照数据模板的格式准备导入数据，模板中的表头名称不可更改，表头行不能删除</p>
                  <a
                    :href="fileImportUrl2"
                    style="cursor: pointer;color:#00aaee;"
                    class="line-padding-import-introduce-step"
                  >下载模版</a>
                </div>
              </div>
            </div>
            <div class="import-introduce-step">
              <div class="import-introduce-step-left line-height-introduce-step" ref="lineHeight">
                 <img src="../assets/u597.png" alt="">
              </div>
              <div class="import-introduce-step-rigth">
                <div class="text-align-left">
                  <p
                    style="font-weight:700;font-size: 16px;"
                    class="line-padding-import-introduce-step"
                  >上传填好的零部件表</p>
                  <p
                    class="line-padding-import-introduce-step"
                  >文件后缀名必须为xls 或xlsx （即Excel格式），文件大小不得大于10M</p>

                  <a-upload
                    :confirmLoading="confirmLoading"
                    name="file"
                    :action="fileImportUrl"
                    :multiple="true"
                    :fileList="importFileList"
                    :showUploadList="false"
                    @change="handleChange4"
                     :beforeUpload="beforeUpload"
                  >
                    <span v-if="loading">
                      <a-icon type="loading" />正在上传，请稍等
                    </span>
                    <a v-else style="color:#00aaee;" class="line-padding-import-introduce-step">上传文档</a>
                  </a-upload>
                </div>
              </div>
            </div>
          </div>
          <div v-if="current === 1" @nextStep="nextStep" @prevStep="prevStep">
            <div class="import-introduce-step2">
              <div class="import-introduce-step-left2">
                   <!-- <img src="../assets/u579.png" alt=""> -->
                   <a-icon type="check-circle"  :style="{ color: '#999',fontSize: '32px'  }"/>

              </div>
              <div class="import-introduce-step-rigth2">
                <div class="text-align-left">
                  <p
                    style="font-weight:700;font-size: 16px;"
                    class="line-padding-import-introduce-step"
                  >本次可导入数量</p>
                  <p
                    class="line-padding-import-introduce-step"
                    style="color:#00aaee;font-size: 16px;"
                    v-if="messageadd"
                  >{{messageadd}}条</p>
                   <p
                    class="line-padding-import-introduce-step"
                    style="color:#00aaee;font-size: 16px;"
                    v-else
                  >0条</p>
                </div>
              </div>
            </div>
            <div class="import-introduce-step2">
              <div class="import-introduce-step-left2">
                   <!-- <img src="../assets/u597.png" alt=""> -->
                   <a-icon type="info-circle" :style="{ color: '#999',fontSize: '32px'  }"/>
              </div>
              <div class="import-introduce-step-rigth2">
                <div class="text-align-left">
                  <p
                    style="font-weight:700;font-size: 16px;"
                    class="line-padding-import-introduce-step"
                  >本次不可导入客户数量</p>
                  <p
                    class="line-padding-import-introduce-step"
                    v-if="messagefal"
                  >
                    
                   <a :href="importUrl" style="color:#00aaee;font-size: 16px;curser:point">{{messagefal}}条</a>
                  </p>
                     <p
                    class="line-padding-import-introduce-step"
                    style="color:#00aaee;font-size: 16px;"
                    v-else
                  >0条</p>
                </div>
              </div>
            </div>
          </div>

          <div v-if="current === 2" @prevStep="prevStep" @finish="finish">
            <a-icon
              type="check-circle"
              style="color:#4BD863;font-size:60px;padding:10px 0 10px;"
              theme="filled"
            />
            <p style="font-size: 24px;font-weight: 700;padding:5px;">批量导入完成</p>
            <p style="padding:15px 0;" v-if="messageadd==0">成功导入客户数量:0条</p>
            <p style="padding:15px 0;" v-else>成功导入客户数量:{{messageadd}}条</p>
          </div>
        </div>
        <div class="steps-action">

          <a-button v-if="current < steps.length - 1" type="primary" @click="next">下一步</a-button>

          <a-button
            v-if="current == steps.length - 1"
            type="primary"
            @click="successUpload()"
          >完成</a-button>
          <a-button v-if="current>0 && current!==2" style="margin-left: 8px" @click="prev">返回重新上传</a-button>
        </div>
      </div>

      <!-- <div class="import-introduce" element-loading-text="数据导入中...">
        <div class="import-introduce-title">根据导入模版填写信息进行导入，即可完成批量添加</div>

        <div class="import-introduce-step">
          <div class="import-introduce-step-left">第一步：</div>
          <div class="import-introduce-step-rigth">
            <div class="import-introduce-step-rigth-top">
              <a :href="fileImportUrl2" style="cursor: pointer">下载Execl导入模版</a>
            </div>
            <div class="import-introduce-step-right-bottom">若已下载过模版，可直接进行导入数据</div>
          </div>
        </div>

        <div class="import-introduce-step">
          <div class="import-introduce-step-left">第二步：</div>
          <div class="import-introduce-step-rigth">
            <div class="import-introduce-step-rigth-top">
              <a-upload
                :confirmLoading="confirmLoading"
                name="file"
                :action="fileImportUrl"
                :multiple="true"
                :fileList="importFileList"
                :showUploadList="false"
                @change="handleChange4"
              >
                <span v-if="loading">
                  <a-icon type="loading" />正在上传，请稍等
                </span>
                <a-button v-else>
                  <a-icon type="upload" />上传文档
                </a-button>
              </a-upload>
            </div>
            <div class="import-introduce-step-right-bottom">若导入数据超过5000条，请分次导入</div>
          </div>
        </div>
      </div>-->
    </a-modal>

    <!-- 过滤条件弹框 -->
    <a-modal title="选择过滤条件" v-model="visibleFilter" @ok="handleOkFilter" class="filter-checkbox">
      <a-checkbox-group @change="onChange" v-model="chooseVal">
        <a-checkbox
          v-for="item in plainOptions"
          :key="item.scrId"
          :value="item.scrId"
          class="filter-check-box"
        >{{item.name}}</a-checkbox>
      </a-checkbox-group>

      <div class="six-choose">注:最多选择6个条件</div>
      <div class="six-more" v-show="checkedValuesFilter">
        <a-icon
          type="info-circle"
          style="color:#faad14;font-size: 14px;margin-right:5px;"
          theme="filled"
        />选择数超过6个,请重新选择
      </div>
    </a-modal>
  </div>
</template>
<script>
import {
  getProductNoTreeList,
  addClassification,
  deletClassification,
  updateClassification,
  DataDictionaryDataList,
  ClassificationDetails,
  Selectcheckboxlist,
  SelectcheckboxlistSave,
  getClassificationList
} from "@/api/ProductApi";
import EditableCell from "./EditableCell";
const columnsfilter = [
  {
    title: "属性名称",
    dataIndex: "attName",
    key: "attName",
    width: "20%",
    align: "center",
    scopedSlots: { customRender: "attName" }
  },
  {
    title: "属性类型",
    dataIndex: "attType",
    key: "attType",
    width: "20%",
    align: "center",
    scopedSlots: { customRender: "attType" }
  },
  {
    title: "允许空值",
    dataIndex: "attIsempty",
    key: "attIsempty",
    width: "20%",
    align: "center",
    scopedSlots: { customRender: "attIsempty" }
  },
  {
    title: "字段值",
    dataIndex: "attValue",
    key: "attValue",
    width: "20%",
    align: "center",
    scopedSlots: { customRender: "attValue" }
  },
  {
    title: "操作",
    key: "action",
    align: "center",
     width: "20%",
    scopedSlots: { customRender: "operation" }
  }
];
const columnsModifyClassification = columnsfilter;
// const plainOptions = ["条12", "条13", "条14","条15","条16","条17","1条件", "2条件", "3条件","4条件","5条件","6条件","11条件", "21条件", "31条件","41条件","51条件","61条件"];

const columns = [
  // {
  //   title: "",
  //   dataIndex: "index1",
  //   key: "index1",
  //   width: "5%",
  //   scopedSlots: { customRender: "index1" }
  // },
  // {
  //   title: "",
  //   dataIndex: "index",
  //   key: "index",
  //   width: "5%",
  //   scopedSlots: { customRender: "index" }
  // },
  {
    title: "分类编号",
    dataIndex: "claCode",
    key: "claCode",
    width: "30%",
    align: "left",
    scopedSlots: { customRender: "claCode" }
  },
  // {
  //   title: "分类名称",
  //   dataIndex: "title2",
  //   key: "title2",
  //   width: 200,
  //   scopedSlots: { customRender: "title2" }
  // },
  {
    title: "分类名称",
    dataIndex: "claName",
    key: "claName",
    width: "35%",
    scopedSlots: { customRender: "claName" }
  },
  {
    title: "操作",
    width: "30%",
    key: "operation",
    align: "center",
    scopedSlots: { customRender: "action" }
  }
];

const rowSelection = {};

export default {
  components: {
    EditableCell
  },
  data() {
    return {
      rowSelection,
      pageSizeOptions: ["10", "20", "30", "40", "50"],
      currentpage: 1,
      pageSize: 10,
      pageNumber: 1,
      total: 0,
      SelectAllowedArr: [],
      SelectTypeArr: [],
      searchkey: "",
      searchtitle: "",
      importFileList: [],
      visible5: false,
      visibleFilter: false,
      addClassificationForm: this.$form.createForm(this),
      editClassificationForm: this.$form.createForm(this),
      addClassificationVisible: false,
      editClassificationVisible: false,
      confirmLoading: false,
      showPagination: false,
      data: [],
      products: [],
      columns,
      columnsfilter,
      columnsModifyClassification,
      dataSourceModifyClassification: [],
      ChangeSelectAllowed: 1,
      current: 0,
      steps: [
        {
          title: "上传文件"
        },
        {
          title: "执行导入"
        },
        {
          title: "导入完成"
        }
      ],

      dataSource: [
        // {
        //   key: '1',
        //   attIsempty:'',
        //   attName: '',
        //   attType: '',
        //   attValue: '',
        //    type: null,
        //    select:null
        // }
      ],
      dataSourcefilter: [],
      curparentId: 0,
      curclaId: 0,
      loading: false,
      valuefliter: [],
      plainOptions: [],
      checkedValuesFilter: false,
      chooseVal: [],
      addindex: 0,
      addindexModify: 0,
      Modifydata: [],
      filterclaCode: "", //筛选的claId
      filterclaId:0,
      checkedForm: [],
      messageadd:'', //上传Excel的add
      messagefal:'',
      importUrl:'',
       uploadSize: 1024 * 1024 * 10,
    };
  },
  computed: {
    fileImportUrl() {
      return (
        this.$global.baseUrl +
        "/classification/import-excel" +
        "?access_token=" +
        this.$global.access_token
      );
    },
    fileImportUrl2() {
      return (
        this.$global.baseUrl +
        "/classification/create-excel" +
        "?access_token=" +
        this.$global.access_token
      );
    }
  },
  created() {
    this.getProductNoTreeLists();
  },
  methods: {
    //获取分类树无主节点列表
    getProductNoTreeLists(type) {
      getClassificationList({page:this.pageNumber,pageSize:this.pageSize,claCode:this.searchkey,claName:this.searchtitle}).then(
        res => {
          if (res.data.code === this.$global.successCode) {
             this.data = res.data.data.list
              this.total = res.data.data.total
            console.log(res.data.data.list);
            if(type == 2){
              this.data = res.data.data.list.map(v => {
             return {
                // title: v.title,
                children:v.children ?v.children:null,
                claId: v.claId,
                key: v.key,
                parentId: v.parentId,
                claName:v.claName,
                claCode:v.claCode
                // value: v.value
              };
            });
            }else{
               this.data = res.data.data.list.map(v => {
             return {
                // title: v.title,
                children:v.children.length ?v.children:null,
                claId: v.claId,
                key: v.key,
                parentId: v.parentId,
                claName:v.claName,
                claCode:v.claCode
                // value: v.value
              };
            });
            }
           

            // this.products = res.data.data;
            this.getForwardNode(this.data, 0);
          } else {
            this.$message.error(res.data.message);
          }
        },
        function(error) {
          console.log("请求数据失败", error);
        }
      );

   
    },
    
    getForwardNode(treedata, level) {
      console.log(treedata, level);
      for (let i = 0; i < treedata.length; i++) {
        treedata[i].level = level + 1;
        if (treedata[i].children) {
          console.log(treedata[i].children,'treedata[i].children',i);
         if(treedata[i].children.length){
             treedata[i].children.map((v)=>{
               if(v.children.length==0){
                 v.children = null
               }
             })
         }
          this.getForwardNode(treedata[i].children, treedata[i].level);
        }
      }
    },

    //新增主分类
    showAddClassificationVisible(corparentId) {
      this.dataSource = [];
      this.addClassificationForm.resetFields();
      this.addClassificationVisible = true;
      this.curparentId = corparentId;
      // console.log(corparentId);
      // 获取数据字典中的 允许空值
      DataDictionaryDataList({ dataType: "attIsempty" }).then(res => {
        // console.log(res.data.data);
        this.SelectAllowedArr = res.data.data;
      });
      //  获取数据字典中的 属性类型
      DataDictionaryDataList({ dataType: "attType" }).then(res => {
        // console.log(res.data.data);
        this.SelectTypeArr = res.data.data;
      });
    },
    handleAddClassificationVisible(e) {
      this.addClassificationVisible = false;
    },
    // 新增
    addClassificationSubmit(e) {
      e.preventDefault();
      this.addClassificationForm.validateFields((err, values) => {
        if (!err) {
          // console.log('Received values of form: ', values);
          // console.log(this.dataSource);
          const form = values;
          form.parentId = this.curparentId;

          let addform = {
            productClassificationAttrForm: this.dataSource,
            claCode: values.claCode,
            claName: values.claName,
            parentId: values.parentId
          };
          console.log(addform);
          //表单数据验证完成之后，提交数据;
          // console.log("form", form);
          addClassification(addform).then(
            res => {
              // console.log("res", res)
              if (res.data.code === this.$global.successCode) {
                //console.log("请求接口成功",res);
                this.$message.success("添加分类成功!");
                this.getProductNoTreeLists();
                this.addClassificationVisible = false;
              } else {
                this.$message.error(res.data.message);
              }
            },
            function(error) {
              console.log("添加分类失败", error);
            }
          );
        }
      });
    },

    //修改主分类
    showEditClassificationVisible(corRow) {
      console.log(corRow);
      this.dataSourceModifyClassification  =[]  //置空 防止可编辑选框里面的text 出现问题
      //this.editClassificationForm.resetFields();
      // 获取数据字典中的 允许空值
      DataDictionaryDataList({ dataType: "attIsempty" }).then(res => {
        // console.log(res.data.data);
        this.SelectAllowedArr = res.data.data;
      });
      //  获取数据字典中的 属性类型
      DataDictionaryDataList({ dataType: "attType" }).then(res => {
        // console.log(res.data.data);
        this.SelectTypeArr = res.data.data;
      });
      // 获取原有的值
      ClassificationDetails(corRow.claId).then(res => {
        this.dataSourceModifyClassification = res.data.data.productClassificationAttrForm;
         this.dataSourceModifyClassification = this.dataSourceModifyClassification.map((val, i) => {
          return{
            attIsempty: val.attIsempty,
            attName: val.attName,
            attType: val.attType,
            attValue: val.attValue,
            claAttId: val.claAttId,
            key: i + 1
          }
        });
        console.log(this.dataSourceModifyClassification,'dataSourceModifyClassification');
      });

      this.editClassificationVisible = true;
      setTimeout(() => {
        //加了个定时器，避免了表单初始化的时候报错
        this.editClassificationForm.setFieldsValue({
          claCode: corRow.claCode,
          claName: corRow.claName
        });
      }, 0);
      this.curclaId = corRow.claId;
      this.curparentId = corRow.parentId;
    },
    handleEditClassificationVisible(e) {
      this.editClassificationVisible = false;
    },
    // 修改提交按钮
    eidtClassificationSubmit(e) {
      let switchbtn = true
      console.log(  this.Modifydata);
      // console.log(this.dataSourceModifyClassification);
      e.preventDefault();
      this.editClassificationForm.validateFields((err, values) => {
        if (!err) {
          // console.log('Received values of form: ', values);
          const form = values;
          form.parentId = this.curparentId;
          form.claId = this.curclaId;
          // console.log(form);

          let Modifyform = {
            productClassificationAttrForm: this.dataSourceModifyClassification,
            claCode: values.claCode,
            claId: values.claId,
            claName: values.claName,
            delProductClassificationAttrForm: this.Modifydata,
            parentId: values.parentId
          };
           console.log(Modifyform);
           Modifyform.productClassificationAttrForm.map((v)=>{
             if(v.attIsempty == '' || v.attName =='' || v.attType=='' || v.attValue==''){
               this.$message.warning('请填写完整的属性！')
               switchbtn = false
             }
           })
          // console.log('表单数据验证完成之后，提交数据');
          //表单数据验证完成之后，提交数据;
          if(switchbtn){
             updateClassification(Modifyform).then(
            res => {
              if (res.data.code === this.$global.successCode) {
                //console.log("请求接口成功",res);
                this.$message.success("更新分类成功!");
                this.getProductNoTreeLists();
                this.editClassificationVisible = false;
              } else {
                this.$message.error(res.data.message);
              }
            },
            function(error) {
              console.log("更新分类失败", error);
            }
          );
        }
          
        }
      });
    },

    //删除主分类
    onDelete(claId) {
      console.log(claId);
      deletClassification(claId).then(
        res => {
          if (res.data.code === this.$global.successCode) {
            //console.log("删除联系方式数据成功",res);
            this.$message.success("删除分类成功!");
            this.getProductNoTreeLists();
          } else {
            this.$message.error(res.data.message);
          }
        },
        function(error) {
          console.log("删除分类失败", error);
        }
      );
    },

    //批量导入数据
    handleChange4(info) {
      console.log(info.fileList);
      
    
      if (info.file.status == "done") {
        console.log(info);
        if ((info.file.response.code = this.$global.successCode)) {
          this.$message.success(info.file.response.message);
          console.log(info.file.response);
         this.messageadd = info.file.response.data.add
         this.messagefal = info.file.response.data.fal
         this.current++
         this.importUrl = info.file.response.data.url +'?access_token='+this.$global.access_token;
        } else {
          this.$message.error(info.file.response.message);
        }
        this.loading = false;
        this.getProductNoTreeLists();
        // this.visible5 = false;

      }
      let fileList = [...info.fileList];
     
      fileList = fileList.slice(-2);
       console.log(fileList);
      fileList = fileList.map(file => {
        return file;
      });
      this.importFileList = fileList;
      console.log( this.importFileList,'=====importFileList');

      // if(info.fileList.length ==1){
      //   console.log(this.$refs.lineHeight); 
      //   this.$refs.lineHeight.style.height = 154 + 'px'
      //   this.$refs.lineHeight.style.lineHeight = 154 + 'px'
      // }else if(info.fileList.length ==2){
      //   this.$refs.lineHeight.style.height = 184 + 'px'
      //   this.$refs.lineHeight.style.lineHeight = 184 + 'px'
      // }
    },
    showModal5() {
      this.visible5 = true;
      this.current = 0;
    },
    handleCancel5(e) {
      this.visible5 = false;
    },

    //搜索
    searchSubmit() {
      // console.log(this.data);
      if(this.searchkey =='' && this.searchtitle==''){
          this.getProductNoTreeLists()
      }else{
          this.getProductNoTreeLists(2)
      }
      //        this.getSearchNode(this.data);
      // let key = this.searchkey;
      // let title = this.searchtitle;
      // let treeData = JSON.parse(JSON.stringify(this.products));
      // console.log(
      //   "this.searchkey",
      //   this.searchkey,
      //   "this.searchtitle",
      //   this.searchtitle
      // );
      // if (key || name) {
      //   this.searchTreeData(key, title, treeData);
      // }
      // this.data = treeData;

      // console.log("this.data", this.data);
    },
    searchTreeData(key, title, treeData) {
      let flag = false;
      if (treeData && treeData.length > 0) {
        for (let i = 0; i < treeData.length; i++) {
          let f = false;
          if (
            (!key || treeData[i].key.indexOf(key) != -1) &&
            (!title || treeData[i].title.indexOf(title) != -1)
          ) {
            f = true;
          }

          if (this.searchTreeData(key, title, treeData[i].children) || f) {
            flag = f;
            if (treeData[i].children.length == 0) {
              treeData[i].children = null;
            }
          } else {
            //                       console.log("key:" + treeData[i].key + ",false")
            treeData.splice(i, 1);
            i--;
          }
        }
      }
      return flag;
    },

    getSearchNode(treedata) {
      let searchkey = this.searchkey;
      let searchtitle = this.searchtitle;
      for (let i = 0; i < treedata.length; i++) {
        if (treedata[i].key === searchkey || treedata[i].key === searchtitle) {
          this.getSearchNode(treedata[i]);
        }
      }
    },
    // 过滤条件
    filter(recode) {

      console.log(recode);
     let claCode = recode.claCode
      this.chooseVal = [];
      this.filterclaCode = claCode;
      this.filterclaId = recode.claId
      this.visibleFilter = true;
      // console.log(claId);
      Selectcheckboxlist({ claCode: claCode }).then(res => {
        if(res.data.code == this.$global.successCode){
           console.log(res.data.data);
        this.plainOptions = res.data.data;
        this.plainOptions.map((val, i) => {
          if (val.selection === true) {
            this.chooseVal.push(val.scrId);
          }
        });
        }else{
          this.$message.warning(`${res.data.message}`)
        }
      
      });
      console.log(this.chooseVal);
    },
    // 过滤弹框的确定按钮
    handleOkFilter(e) {
      console.log(this.chooseVal);
    
      if (this.chooseVal.length > 6) {
        this.$message.warning("选择数超过6个,请重新选择");
      } 
      else {
        console.log(this.plainOptions);
        SelectcheckboxlistSave(this.plainOptions).then(res => {
          console.log(this.$global.successCode);
          if (res.data.code == this.$global.successCode){
            this.$message.success("选择过滤条件成功！");
            this.visibleFilter = false;
          }else{
             this.$message.warning(`${res.data.message}`)
          }
           
        });
      }
    },
    onChange(checkedValues) {
      console.log(checkedValues);
      let checkedValArr = [],
        uncheckedArr = [];
      this.chooseVal =checkedValues
      console.log(this.chooseVal);
      this.plainOptions.map((val, i) => {
        if(checkedValues.length){
          checkedValues.map((item, index) => {
          if (val.scrId == item) {
            //  val.selection = true
            //  val.claId =  this.filterclaCode
            checkedValArr.push(val);
            this.checkedForm = checkedValArr;
          } else {
            val.selection = false;
          }
        });
        }else{
          val.selection = false;
          this.checkedForm =[]
        }
        
      });
      // map 使用在引用值类型的时候，会改变原数组的值
      this.checkedForm.map(v => {
        v.selection = true;
        v.claCode = this.filterclaCode;
        v.claId =  this.filterclaId
      });
      console.log(this.checkedForm);
      console.log(this.plainOptions);
     
      if (checkedValues.length > 6) {
        this.checkedValuesFilter = true;
      } else {
        this.checkedValuesFilter = false;
      }
    },
    newMeber() {
      this.addindex = this.dataSource.length;
      this.addindex = this.addindex + 1;
      console.log(this.addindex);
      this.dataSource.push({
        key: this.addindex,
        attIsempty: "",
        attName: "",
        attType: "",
        attValue: ""
      });
    },
    newMeberModifyClassification() {
      this.addindexModify = this.dataSourceModifyClassification.length;
      this.addindexModify = this.addindexModify + 1;
      console.log(this.addindexModify);
      this.dataSourceModifyClassification.push({
        key: this.addindexModify,
        attIsempty: "",
        attName: "",
        attType: "",
        attValue: ""
      });
    },
    // 新增的删除
    removefilter(key) {
      const newData = this.dataSource.filter(item => item.key !== key);
      this.dataSource = newData;
    },
    removeModify(key, record) {
      console.log(key, record);
      if (record.claAttId){
        this.Modifydata.push(record)
        console.log(this.Modifydata,'this.Modifydata');
        }
      // console.log(this.Modifydata ,'=====this.Modifydata ');
      const newData = this.dataSourceModifyClassification.filter(
        item => item.key !== key
      );
      this.dataSourceModifyClassification = newData;

      console.log( this.dataSourceModifyClassification);
    },
    handleChangeSelectType(value, key, column) {
      console.log(value, key, column);
      const newData = [...this.dataSource];
      const target = newData.filter(item => key === item.key)[0];
      if (target) {
        target[column] = value;
        this.dataSource = newData;
      }
    },
    handleChangeSelectAllowed(value, key, column) {
      //  let value = this.ChangeSelectAllowed
      console.log(value, key, column);
      const newData = [...this.dataSource];
      const target = newData.filter(item => key === item.key)[0];
      if (target) {
        target[column] = value;
        this.dataSource = newData;
      }
    },

    // 增加弹框失去焦点报红
    blurRed() {
      this.addClassificationForm.validateFields(["claCode"], (err, values) => {
        if (!err) {
        }
      });
    },
    blurRedName() {
      this.addClassificationForm.validateFields(["claName"], (err, values) => {
        if (!err) {
        }
      });
    },
    onCellChangeattName(key, dataIndex, value) {
      const dataSource = [...this.dataSource];
      const target = dataSource.find(item => item.key === key);
      if (target) {
        target[dataIndex] = value;
        this.dataSource = dataSource;
      }
    },
    onCellChangeattValue(key, dataIndex, value) {
      const dataSource = [...this.dataSource];
      const target = dataSource.find(item => item.key === key);
      if (target) {
        target[dataIndex] = value;
        this.dataSource = dataSource;
      }
    },
    onCellChangeattNameModify(key, dataIndex, value) {
      const dataSource = [...this.dataSourceModifyClassification];
      const target = dataSource.find(item => item.key === key);
      if (target) {
        target[dataIndex] = value;
        this.dataSourceModifyClassification = dataSource;
      }
    },
    onCellChangeattValueModify(key, dataIndex, value) {
      const dataSource = [...this.dataSourceModifyClassification];
      const target = dataSource.find(item => item.key === key);
      if (target) {
        target[dataIndex] = value;
        this.dataSourceModifyClassification = dataSource;
      }
    },
    // 步骤条
    next() {
      this.current++;
    },
    prev() {
      this.current--;
    },
    handleOkImport() {},
    nextStep() {
      if (this.current < 2) {
        this.current += 1;
      }
    },
    prevStep() {
      if (this.current > 0) {
        this.current -= 1;
      }
    },
    finish() {
      this.current = 0;
    },
    successUpload(){
      this.visible5 = false;
    },
    // 分页
    // 右边的页码
    onChangepagination(pageNumber) {
      this.pageNumber = pageNumber;
      // console.log("Page: ", pageNumber);
      this.getProductNoTreeLists();
    },
      onShowSizeChange(current, pageSize) {
      this.pageSize = pageSize;
       this.pageNumber = 1
      // console.log(this.pageSize);
      // console.log(current);
      this.getProductNoTreeLists();
    },
    // 批量导入
   beforeUpload(file, files) {
     console.log(file, files);
      if (
        file.type != "application/vnd.ms-excel" &&
        file.type !=
          "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
      ) {
        this.$message.error("文件后缀名必须为xls或xlsx(即excel格式)");
        return false;
      }
      if (file.size > this.uploadSize) {
        this.$message.error("大小不得大于10M");
        return false;
      }
      return true;
    },
  }
};
</script>
